import { Component, OnInit } from '@angular/core';
import { 
  Settings, 
  DefinedSensitivity
} from 'ngx-sticky-navbar';
import * as anime from 'animejs';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit{
  title = 'portfolio';
  profession;
  profarr=['Developer', 'UI Designer', 'Student', 'Minimalist'];
  index=0;

  blur:boolean=false;
  settings: Settings = {
    scroll: {
      element: '.scrollable'
    },
    sensitivity: {
      top: DefinedSensitivity.veryLow, // or 50
      bottom: DefinedSensitivity.veryHigh // or 10
    }
  };

  profchange(){
    this.index++;
    this.profession= this.profarr[this.index%this.profarr.length]
  }

  onmousemovedef(){
    var element = document.getElementById("mainbg");

    if(this.blur==true){
    element.setAttribute("style", "-webkit-filter: blur(4px);   filter: blur(4px);");
    }
    else{
      element.setAttribute("style", "-webkit-filter: blur(4px);   filter: blur(0px);");
    }

  }

  copyText(val: string){
    let selBox = document.createElement('textarea');
      selBox.style.position = 'fixed';
      selBox.style.left = '0';
      selBox.style.top = '0';
      selBox.style.opacity = '0';
      selBox.value = val;
      document.body.appendChild(selBox);
      selBox.focus();
      selBox.select();
      document.execCommand('copy');
      document.body.removeChild(selBox);
    }

  ngOnInit(){
    this.profession= this.profarr[this.index];
  }
}
